#Language {
  position: fixed;
  bottom: 25px;
  left: 20px;
  z-index: 1000;
  -webkit-transition: color .4s ease-out;
  -o-transition: color .4s ease-out;
  transition: color .4s ease-out;
  overflow: hidden; }
  @media (min-width: 768px) {
    #Language {
      left: 50px;
      bottom: 50px; } }
  @media (max-height: 500px) {
    #Language {
      bottom: 25px; } }
  #Language.black-color {
    color: #000000; }
  #Language .button {
    font-weight: 700;
    text-transform: uppercase; }

@media (max-width: 575px) {
  .active-form #Language {
    color: #000000; } }

.language-item {
  position: relative; }

#Navigation .language-item {
  position: static; }



